<template>
  <div>
    <v-container class="admin-problemas-tipos__container">
      <v-row>
        <v-col>
          <show-dialog-tipo-problema
            v-bind="{ value }"
            @procesar="procesar($event)"
            @cerrar="cerrar()"
          />
          <base-material-card
            color="success"
            icon="mdi-view-carousel"
            inline
            title="Tipos de Problema"
            class="px-5 py-3 mb-5"
          >
            <v-row class="admin-problemas-tipos_row__nuevo">
              <v-col class="text-right">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="success ma-0"
                      v-bind="attrs"
                      @click="abrirCrear()"
                      v-on="on"
                    >
                      Nuevo Tipo
                    </v-btn>
                  </template>
                  <span>Crea un nuevo Tipo de Problema</span>
                </v-tooltip>
              </v-col>
            </v-row>
            <v-simple-table class="admin-problemas-tipos__table">
              <thead>
                <tr>
                  <th>Codigo</th>
                  <th>Nombre</th>
                  <th style="text-align:right;">
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr
                  v-for="(item, i) in tipos"
                  :key="i"
                >
                  <td>{{ item.CodTipoProblema }}</td>
                  <td>{{ item.NombreTipoProblema }}</td>
                  <td class="text-right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="warning btn-edit"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="abrirEditar(item.CodTipoProblema)"
                        >
                          <v-icon>mdi-pencil</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="error btn-delete"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="
                            eliminar(
                              item.CodTipoProblema,
                              item.NombreTipoProblema
                            )
                          "
                        >
                          <v-icon>
                            mdi-trash-can
                          </v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
          </base-material-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { mapState, mapMutations } from 'vuex'
  import swalAlertError from '@/mixins'
  export default {
    name: 'Problemas',
    components: {
      ShowDialogTipoProblema: () =>
      import('@/components/base/ShowDialogTipoProblema'),
    },
    mixins: [swalAlertError],
    data () {
      return {
        value: {
          active: false,
          tipoProblema: {
            nombre: '',
            toLink: '',
            CodTipoProblema: '',
            imagen: '',
            descripcion: '',
            tratamiento: '',
            causas: '',
            problemasTitulo: '',
            tratamientoTitulo: '',
            causasTitulo: '',
            metaTitle: '',
          },
          problemas: [],
          imagen: null,
          loader: false,
        },
        tipos: '',
      }
    },
    computed: {
      ...mapState(['token']),
    },
    created () {
      this.getTipos()
    },
    methods: {
      ...mapMutations(['cerrarSesion']),
      getTipos () {
        Vue.axios
          .post('/api/problemas-tipos', { fromAdmin: true })
          .then(res => {
            this.tipos = res.data.result
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getTipo (codTipoProblema) {
        Vue.axios
          .post('/api/problemas-tipos/tipo', { codTipoProblema, fromAdmin: true })
          .then(res => {
            this.value.tipoProblema = res.data.tipoProblema
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getProblemas (codTipoProblema = null) {
        Vue.axios
          .post('/api/problemas', { codTipoProblema })
          .then(res => {
            this.value.problemas = res.data.problemas
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      abrirCrear () {
        this.getProblemas()
        this.value.active = true
      },
      abrirEditar (codTipoProblema) {
        this.getTipo(codTipoProblema)
        this.getProblemas(codTipoProblema)
        this.value.active = true
      },
      eliminar (codTipoProblema, nombreTipoProblema) {
        Vue.swal({
          title: `Estás seguro que deseas borrar ${nombreTipoProblema}?`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.swal('Problema Eliminado', '', 'success')
            Vue.axios
              .delete(`/api/problemas-tipos/${codTipoProblema}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.tipos.findIndex(
                  i => i.CodTipoProblema === Number(res.data.codTipoProblema),
                )
                this.tipos.splice(index, 1)
              })
              .catch(e => {
                this.cerrar()
                this.swalAlertError(e)
              })
          }
        })
      },
      create (body, headers) {
        Vue.axios
          .post('/api/problemas-tipos/crear', body, { headers })
          .then(res => {
            this.tipos.unshift(res.data.TipoProblema[0])
            this.cerrar()
            Vue.swal('Problema Creado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      update (body, headers) {
        console.log('editando')
        Vue.axios
          .put('/api/problemas-tipos/editar', body, { headers })
          .then(res => {
            const index = this.tipos.findIndex(
              tipo => tipo.CodTipoProblema === Number(res.data.codTipoProblema),
            )
            this.tipos[index].NombreTipoProblema = this.value.tipoProblema.nombre
            this.cerrar()
            Vue.swal('Problema Actualizado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      procesar (val) {
        console.log('procesando...')
        /* Arr problemas se va vacío, salvo que haya problemas checkeados */
        const problemas = []
        if (
          val.problemas.find(
            problema => problema.checked != null && problema.checked !== false,
          )
        ) {
          for (let i = 0; i < val.problemas.length; i++) {
            if (val.problemas[i].checked) {
              problemas.push(Number(val.problemas[i].CodProblema))
              console.log(Number(val.problemas[i].CodProblema))
            }
          }
        }

        if (problemas.length === 0) {
          Vue.swal('Debe asociar al menos un problema de la lista', '', 'error')
          return false
        }

        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        const body = new FormData()
        body.append('codTipoProblema', val.tipoProblema.CodTipoProblema)
        body.append('nombre', val.tipoProblema.nombre)
        body.append('descripcion', val.tipoProblema.descripcion)
        body.append('tratamiento', val.tipoProblema.tratamiento)
        body.append('causas', val.tipoProblema.causas)
        body.append('toLink', val.tipoProblema.toLink)
        body.append('problemasTitulo', val.tipoProblema.problemasTitulo)
        body.append('tratamientoTitulo', val.tipoProblema.tratamientoTitulo)
        body.append('causasTitulo', val.tipoProblema.causasTitulo)
        body.append('metaTitle', val.tipoProblema.metaTitle)
        body.append('imagen', val.imagen)
        body.append('problemas', problemas)

        if (val.tipoProblema.CodTipoProblema) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      cerrar () {
        this.value.active = false
        this.value.tipoProblema = {}
        this.value.imagen = null
        this.value.loader = false
      },
    },
    metaInfo: {
      title: 'Admin - Problemas',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
